.clients {
  padding: 67px 0 80px 0;
  background-color: $gray-lightest;

  &__container {
    margin: 0 auto;
    padding: 0 20px;
    width: 100%;

    @media (min-width: $screen-md) {
      padding: 0;
      width: 760px;
    }

    @media (min-width: $screen-lg) {
      padding: 0;
      width: 920px;
    }
  }

  &__row {
    display: flex;
    align-items: center;
    margin-bottom: 70px;
    overflow-x: auto;

    &::-webkit-scrollbar {
      display: none;
    }

    @media (min-width: $screen-md) {
      justify-content: space-between;
    }
  }

  &__img {
    margin-right: 30px;

    @media (min-width: $screen-md) {
      margin-right: 0;
    }
  }

  &__review {

    @media (min-width: $screen-md) {
      padding: 0 40px;
    }

    blockquote {
      margin-bottom: 40px;
      text-align: center;
      font-family: $second-font-family;
      color: $second-color;

      p {
        margin-bottom: 35px;
        line-height: 30px;
        font-size: 18px;
        font-style: italic;
      }

      cite {
        font-size: 20px;
        font-style: normal;
      }
    }
  }
}
